@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-family: 'Ubuntu', sans-serif;}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

header {
    background: #fff;
    box-shadow: 0px 2px 8px 3px #dac2c2;
    padding: 12px 0px;
    font-size: 21px;
    color: #000;
    font-weight: 500;
    font-family: 'Poppins';
}

nav a:hover{
    color: #fb4040;
}
nav a {
    margin: 0px 12px;
    text-decoration: none;
    color: #000;
}
/* 
nav span.fas.fa-search {
    color: #fb4040;
    cursor: pointer;
    margin: 0px 15px;
    font-size: 25px;
} */
.col-lg-10.col-md-10.col-sm-10.text-right {
    text-align: right;
}
.mainContent{
    background-color: #f1d1d6a6;
    padding: 80px 0px;
}
.productCard {
    background: #fff;
    text-align: left;
    margin: 20px 5px;
    /* padding-bottom: 10px; */
    border-radius: 5px;
    box-shadow: 1px 1px 6px #777676;
    cursor: pointer;
    transition: .3s;

}
.productCard:hover{
    box-shadow: 0px 0px 0px #ffffff;
    border-radius: 0px;

}
.dataDetails{
    color: #000;
    display: flex;
    justify-content: space-between;
    align-items: center;

}

.dataDetails span.totalData{
    font-size: 20px;
    font-weight: 700;
    font-family: 'Poppins';
}
.dataDetails input {
    color: black;
    border: none;
    outline: none;
    padding: 7px 15px;
    width: 60%;
    margin-right: 3px;
    border-radius: 40px;
    font-size: 20px;
}


.productCard .productImage img{
        width: 100%;
        height: 240px;

}

.productCard .productDetails h2.title {
    font-size: 20px;
    font-family: 'Ubuntu';
    color: #111;
     margin: 10px 0px 0px 0px;
    font-weight: 600;
    letter-spacing: 1px;
    padding: 0px 8px;

}
.productCard .productDetails span.regularPrice{
    color: #505050;
    font-weight: 600;
    text-decoration: line-through;
    font-size: 19px;
    margin-right: 7px;
    padding: 0px 0px 0px 8px;

}

.productCard .productDetails span.salePrice{
    color: #111;
    font-weight: 700;
    font-size: 22px;
    margin-left: 3px;
}

span.sellerName {
    font-size: 20px;
    font-weight: 500;
    text-align: left;
    color: #e36364;
}

span.rattings {
    float: right;
    font-size: 20px;
    color: goldenrod;
    margin-right: 3px;
}

button.cartBtn {
    color: white;
    border: none;
    background: black;
    width: 100%;
    padding: 8px 30px;
    margin-top: 15px;
    font-family: 'Poppins';
}
button.cartBtn:hover {
    background-color: #111;
    color: white;
    transition: .3s;
}

.shoppingCart {
    background: #fff;
    width: 225px;
    padding: 10px;
    box-shadow: 0px 1px 3px 3px #dac2c2;
    margin: 20px 0px;
    position: fixed;
}
.cartTable{
      display: grid;
    grid-template-columns: repeat(2, 1fr);
}
.tableItems {
    text-align: left;
}

.tableValues {
  text-align: right;
}
